<template>
  <div class="preg-tootle-page">
    <div class="preg-tootle-page-header"></div>
    <div class="preg-tootle-page-body">
      <div class="preg-tootle-page-form-box">
        <div class="preg-tootle-page-form-items"><textarea name="input-text" id="" cols="30" rows="10"
            placeholder="" v-model="pregForm.input"></textarea></div>
        <div class="preg-tootle-page-form-items">
          <label for="preg-str"></label>
          <input type="text" class="preg-str" v-model="pregForm.preg" placeholder="" />
          <span><input type="checkbox" v-model="pregForm.global"></span>
          <span><input type="checkbox" v-model="pregForm.ignore"></span>
          <span><input type="checkbox" v-model="pregForm.multiline"></span>
          <a href="javascript:void(0);" class="exec-but" @click="pregExecBut($event)"></a>
        </div>
        <div class="preg-tootle-page-form-items"><textarea name="input-text" id="" cols="30" rows="10"
            style="background:#eee;" v-model="pregForm.result"></textarea></div>
        <div class="preg-tootle-page-form-items">
          <label for=""></label>
          <input type="text" name="replace-str" class="replace-str" v-model="pregForm.replaceInput"
            placeholder="" />
          <a href="javascript:void(0);" class="replace-exec-but" @click="replaceExecBut($event)"></a>
        </div>
        <div class="preg-tootle-page-form-items"><textarea name="input-text" id="" cols="30" rows="10"
            style="background:#eee;" v-model="pregForm.replaceResult"></textarea></div>
      </div>
      <div class="preg-grammar-box">
        <div class="preg-grammar-header"></div>
        <div class="preg-grammar-body">
          <p class="preg-grammar-items"><span>\d</span>[0-9]</p>
          <p class="preg-grammar-items"><span>\D</span>[^0-9]</p>
          <p class="preg-grammar-items"><span>\s</span>[\n\f\r\t\v]</p>
          <p class="preg-grammar-items"><span>\S</span>[^\n\f\r\t\v]</p>
          <p class="preg-grammar-items"><span>\w</span>()[a-zA-Z0-9_]</p>
          <p class="preg-grammar-items"><span>\W</span>[^a-zA-Z0-9_]</p>
          <p class="preg-grammar-items"><span>\b</span> er\bnevererverber"</p>
          <p class="preg-grammar-items"><span>\B</span> er\Bverberneverer</p>
          <p class="preg-grammar-items"><span>+</span>{0,1}</p>
          <p class="preg-grammar-items"><span>?</span>{1,}</p>
          <p class="preg-grammar-items"><span>{n}</span>n</p>
          <p class="preg-grammar-items"><span>{n,}</span>n</p>
          <p class="preg-grammar-items"><span>{,n}</span>n</p>
          <p class="preg-grammar-items"><span>^</span></p>
          <p class="preg-grammar-items"><span>$</span></p>
          <p class="preg-grammar-items"><span>.</span></p>
          <p class="preg-grammar-items"><span>*</span>{0,}</p>
          <p class="preg-grammar-items"><span>\n|\n\r</span>linux/windows</p>
          <p class="preg-grammar-items"><span>g</span></p>
          <p class="preg-grammar-items"><span>i</span></p>
          <p class="preg-grammar-items"><span>m</span></p>
          <p class="preg-grammar-items"><span>[\u4e00-\u9fa5]</span></p>
          <p class="preg-grammar-items"><span>[^\x00-\xff]</span>()</p>
          <p class="preg-grammar-items"><span>\d+(\.\d+)?</span></p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PregTootl',
  data () {
    return {
      pregForm: {
        input: '',
        preg: '',
        result: '',
        global: true,
        ignore: false,
        multiline: false,
        replaceInput: '',
        replaceResult: ''
      }
    }
  },
  methods: {
    pregExecBut (e) {
      let modifiers = `${this.pregForm.global ? 'g' : ''}${this.pregForm.ignore ? 'i' : ''}${this.pregForm.multiline ? 'm' : ''}`
      let regexp = new RegExp(this.pregForm.preg, modifiers)
      let result = regexp.exec(this.pregForm.input)
      // let result = this.pregForm.input.match(regexp)
      this.pregForm.result = result ? result[0] : ''
    },
    replaceExecBut (e) {
      let modifiers = `${this.pregForm.global ? 'g' : ''}${this.pregForm.ignore ? 'i' : ''}${this.pregForm.multiline ? 'm' : ''}`
      let regexp = new RegExp(this.pregForm.preg, modifiers)
      this.pregForm.replaceResult = this.pregForm.input.replace(regexp, this.pregForm.replaceInput)
    }
  }
}

</script>

